body {
	position: relative;
	font-family: $mfont;
	@include font-smoothing; }

section {
	position: relative;
	padding: 100px 0;
	overflow: hidden;
	@include r($sm) {
		padding: 50px 0; } }

.container {
	width: 100%;
	height: 100%;
	max-width: 1168px;
	margin: 0 auto;
	@include r($sm) {
		max-width: 550px;
		padding: 0 16px;
		width: auto; } }

@mixin transition {
	transition: .3s ease; }

.leave {
	@include r($sm) {
		display: none !important; } }

.decoration {
	width: 100%;
	height: 100%;
	background-color: transparent;
	span {
		position: absolute;
		top: 0;
		width: 1px;
		height: 100%;
		background-color: rgba(#000, .1);
		&:nth-of-type(1) {
			left: 5%; }
		&:nth-of-type(2) {
			left: 20%; }
		&:nth-of-type(3) {
			left: 40%; }
		&:nth-of-type(4) {
			left: 60%; }
		&:nth-of-type(5) {
			left: 80%; }
		&:nth-of-type(6) {
			left: 95%; } } }


.nav {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 90px;
	background-color: #f9f9f9;
	box-shadow: 0 3px 40px rgba(0, 0, 0, 0.1);
	z-index: 100;
	&-inner {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&-logo {}

	&-menu {
		list-style: none;
		width: 40%;
		display: flex;
		justify-content: space-between;
		@include r($sm) {
			order: 3;
			position: absolute;
			top: 0;
			right: -100%;
			width: 100%;
			height: 100vh;
			flex-direction: column;
			justify-content: flex-start;
			padding: 40px;
			padding-left: 70px;
			background-color: $color-darkblue;
			color: #fff; }
		li {
			@include r($sm) {
				margin: 30px 0; }
			a {
				font-size: 16px;
				color: $color-darkblue;
				text-decoration: none;
				@include transition;
				@include r($sm) {
					font-size: 24px;
					color: #fff; }
				&:hover {
					color: $color-red; } } } }

	&-phone {
		font-size: 20px;
		color: $color-darkblue;
		font-weight: 700;
		text-decoration: none;
		@include transition;
		@include r($sm) {
			display: none; }
		&:hover {
			color: $color-red; } } }

.hamburger {
	display: none;
	width: 50px;
	height: 50px;
	background-color: transparent;
	position: relative;
	cursor: pointer;
	z-index: 100;
	@include r($sm) {
		display: block; }
	& span,
	&:before,
	&:after {
		content: " ";
		display: block;
		width: 30px;
		height: 1px;
		background-color: $color-darkblue;
		transform: rotate(0);
		position: absolute;
		left: 10px;
		transition: all 300ms ease-in-out; }
	&:before {
		top: 16px; }
	& span {
		top: 25px;
		opacity: 1; }

	&:after {
		bottom: 15px; }
	&.active span {
		opacity: 0;
		top: 25px;
		transform: rotate(90deg);
		background-color: #fff; }

	&.active:before {
		width: 34px;
		top: 25px;
		left: 8px;
		transform: rotate(45deg);
		background-color: #fff; }

	&.active:after {
		width: 34px;
		bottom: 24px;
		left: 8px;
		transform: rotate(-45deg);
		background-color: #fff; } }

.header {
	position: relative;
	width: 100%;
	height: 120vh;
	padding-top: 150px;
	.leave {
		position: absolute;
		z-index: -1;

		&:first-child {
			top: 160px;
			left: 2.5%; }
		&:last-child {
			bottom: 5%;
			right: 2.5%; } }
	&-inner {
		position: relative;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-between;
		@include r($sm) {
			flex-wrap: wrap; } }
	&-left {
		width: 38%;
		@include r($sm) {
			width: 100%;
			padding-left: 20px; }
		h1 {
			font-size: 56px;
			font-weight: 400;
			color: $color-darkblue;
			margin-bottom: 40px;
			span {
				color: $color-gold; } }
		&-list {
			list-style: none;
			margin: 0;
			padding: 0;
			li {
				position: relative;
				padding-left: 45px;
				margin: 20px 0;
				font-style: italic;
				&:before {
					content: '';
					position: absolute;
					top: 50%;
					left: 0;
					transform: translateY(-50%);
					width: 30px;
					height: 2px;
					background-color: $color-gold; } } } }
	&-right {
		@include r($sm) {
			display: none; }
		&-form {
			position: relative;
			width: 440px;
			height: max-content;
			clip-path: polygon(0% 3%, 100% 0%, 100% 100%, 0% 97%);
			background-color: #f9f9f9;
			padding: 4rem 3rem;
			display: flex;
			flex-direction: column;
			box-shadow: 2px 10px 40px rgba(118, 129, 185, 0.24);
			z-index: 2;

			h2 {
				font-size: 25px;
				color: $color-darkblue;
				span {
					color: $color-gold; } } } } }

@mixin advList {
	content: '';
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	width: 90px;
	height: 90px;
	background-size: contain;
	background-position: center; }

.adv {
	width: 100%;
	background-color: #fafafa;
	&-inner {
		position: relative;
		display: flex;
		z-index: 2; }
	&-list {
		width: 60%;
		@include r($sm) {
			width: 100%; }
		&-item {
			position: relative;
			padding-left: 100px;
			margin: 36px 0;
			&:nth-child(1)::before {
				@include advList;
				background-image: url('../img/adv-list1.png'); }
			&:nth-child(2)::before {
				@include advList;
				background-image: url('../img/adv-list2.png'); }
			&:nth-child(3)::before {
				@include advList;
				background-image: url('../img/adv-list3.png'); }
			&:nth-child(4)::before {
				@include advList;
				background-image: url('../img/adv-list4.png'); }
			&:nth-child(5)::before {
				@include advList;
				background-image: url('../img/adv-list5.png'); }
			&:nth-child(6)::before {
				@include advList;
				background-image: url('../img/adv-list6.png'); }

			p {
				position: relative;
				font-size: 14px;
				font-weight: 700;
				line-height: 1.6;
				color: $color-darkblue;
				&:after {
					content: '';
					position: absolute;
					bottom: -18px;
					left: 0;
					width: 75%;
					height: 1px;
					background-color: rgba(#000, .15); }
				span {
					color: $color-gold; } } } }
	&-pic {
		position: absolute;
		top: 50%;
		right: 5%;
		transform: translateY(-50%);
		@include r($sm) {
			left: 50%;
			transform: translate(-50%, -50%);
			width: 100%;
			opacity: .2;
			img {
				width: 100%; } }
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 620px;
			height: 620px;
			background: url('../img/adv-el.svg');
			background-size: contain;
			@include r($sm) {
				display: none;
				width: 280px;
				height: 280px; } }
		.leave-pic {
			position: absolute;
			bottom: -10%;
			right: -25%; } }

	.leave {
		position: absolute;
		z-index: 0;

		&:nth-child(2) {
			top: 3%;
			left: 0; }

		&:nth-child(3) {
			bottom: 5%;
			left: 1%; } } }

.about {
	position: relative;
	width: 100%;
	&-inner {
		position: relative;
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		grid-gap: 32px;
		@include r($sm) {
			grid-template-columns: 1fr; } }
	&-content {
		grid-column: 1 / span 2;
		@include r($sm) {
			grid-column: 1 / span 1; }
		h2 {
			font-size: 42px;
			font-weight: 400;
			color: #3d485f;
			margin-bottom: 30px; }
		p {
			width: 80%;
			font-size: 15px;
			font-weight: 400;
			color: #0d0d0d;
			line-height: 1.8;
			@include r($sm) {
				width: 100%; } } }

	.leave {
		position: absolute;
		z-index: -1;
		&:nth-child(2) {
			top: 2%;
			left: 3%; }
		&:nth-child(3) {
			top: 3%;
			right: 1%; }
		&:nth-child(4) {
			bottom: 3%;
			right: 2%; } } }

.feed {
	position: relative;
	width: 100%;
	background-color: #fafafa;
	&-inner {
		position: relative;
		height: 520px;
		z-index: 2;

		.glide .glide__bullets {
			bottom: -5em; }

		.glide__bullet {
			width: 13px;
			height: 13px;
			margin: 0 16px;
			background-color: transparent;
			border: 2px solid rgba(#000, .15);
			transition: .3s ease;
			&--active {
				border: 1px solid  #ffc76e;
				background-color:  #ffc76e; } } }

	.leave {
		position: absolute;
		z-index: 0;

		&:nth-child(2) {
			top: 100px;
			left: 2.5%; }
		&:last-child {
			bottom: 5%;
			right: 2.5%; } } }

.delivery {
	position: relative;
	width: 100%;

	.leave {
		position: absolute;
		z-index: -1;

		&:first-child {
			top: 4%;
			left: 2%; }
		&:nth-child(2) {
			bottom: 4%;
			left: 4%; }
		&:nth-child(3) {
			bottom: 6%;
			right: 4%; } }

	&-inner {
		position: relative;
		height: 450px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		@include r($sm) {
			height: max-content; }
		h2 {
			font-size: 42px;
			font-weight: 400; }
		p {
			width: 40%;
			text-align: center;
			font-size: 15px;
			margin-bottom: 60px;
			@include r($sm) {
				width: 80%; } }
		a {
			width: 35%;
			padding: 1.2rem 2.5rem 1.2rem 2.5rem;
			@include r($sm) {
				width: 100%; }
			&::before {
				display: none; } } }

	&-countries {
		width: 85%;
		display: flex;
		justify-content: space-between;
		@include r($sm) {
			flex-wrap: wrap;
			img {
				width: 48%;
				height: 100%;
				margin-bottom: 15px; } } } }

.footer {
	position: relative;
	width: 100%;
	background-color: #1a1d26;
	@include r($sm) {
		height: 620px;
		display: flex;
		flex-direction: column; }
	.container {
		order: 2; }
	&-content {
		padding: 65px 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@include r($sm) {
			padding: 40px 0; }
		h2 {
			font-size: 42px;
			font-weight: 400;
			color: #ffffff; }
		ul {
			margin-bottom: 16px;
			li {
				position: relative;
				padding-left: 50px;
				margin: 20px 0;
				&::before {
					content: '';
					position: absolute;
					top: 50%;
					left: 0;
					transform: translateY(-50%);
					width: 20px;
					height: 20px;
					background-size: contain;
					background-position: 50% 50%;
					background-repeat: no-repeat; }
				&:nth-of-type(1)::before {
					background-image: url('../img/phone.svg'); }
				&:nth-of-type(2)::before {
					background-image: url('../img/location.svg'); }
				&:nth-of-type(3)::before {
					background-image: url('../img/mail.svg'); }
				a, p {
					font-size: 20px;
					color: #fff;
					text-decoration: none; }
				a {
					transition: .3s ease;
					&:hover {
						color: $color-gold; } } } }
		h3 {
			color: #fff;
			margin-bottom: 16px; } }
	&-contacts {
		a {
			position: relative;
			width: 20px;
			height: 20px;
			margin-right: 64px;
			&:nth-of-type(1)::before {
				background-image: url('../img/facebook.svg'); }
			&:nth-of-type(2)::before {
				background-image: url('../img/instagram.svg'); }
			&:nth-of-type(3)::before {
				background-image: url('../img/twitter.svg'); } }
		a::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
			background-size: contain;
			background-position: 50% 50%;
			background-repeat: no-repeat; } }
	&-copy {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50px;
		border-top: 1px solid rgba(#fff, .15);
		display: flex;
		align-items: center;
		.container {
			display: flex;
			align-items: center;
			p {
				color: #fff; } } }
	.map {
		position: absolute;
		top: 0;
		right: 0;
		width: 50%;
		height: 100%;
		@include r($sm) {
			position: relative;
			order: 1;
			height: 200px;
			width: 100%; }
		iframe {
			width: 100%;
			height: 100%; } } }



