.btn {
  position: relative;
  border: none;
  outline: none;
  border-radius: 25px;
  text-decoration: none;
  cursor: pointer;
  transition: .3s ease;
  &-green__outline {
    position: relative;
    width: 300px;
    height: 64px;
    border-radius: 50px;
    position: relative;
    border: 2px solid #3adb40;
    box-shadow: 0 10px 40px rgba(218, 39, 60, 0.4);
    color: #31617f;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    svg {
      margin-left: 20px;
      path {
        fill: #67c15e;
        transition: .3s ease; } }
    span {
      padding-right: 30px;
      font-weight: 700; }
    &:hover {
      color: #fff;
      background-color: #3adb40;
      svg path {
        fill: #fff; } } }
  &-grad__red {
    width: 90%;
    position: relative;
    padding: 1.2rem 2.5rem;
    padding-left: 4rem;
    text-align: center;
    border-radius: 50px;
    background-image: linear-gradient(to right, #eb2940 0%, #a01f2e 100%);
    box-shadow: 0 10px 40px rgba(218, 39, 60, 0.4);
    border: 2px solid transparent;
    color: #fff;
    font-family: $mfont;
    font-weight: 700;
    font-size: 16px;
    margin-top: 32px;
    cursor: pointer;
    transition: .3s ease;
    &:before {
      content: '';
      position: absolute;
      width: 62px;
      height: 62px;
      top: -1rem;
      left: 1.2rem;
      background-image: url('../img/form-btn.png');
      background-size: contain;
      background-position: center; }
    &:hover {
      background-image: linear-gradient(to left, #eb2940 0%, #a01f2e 100%);
      border-color: #a01f2e; } } }

.input {
  padding: 1.2rem 2.5rem;
  margin: 16px 0;
  border-radius: 25px;
  box-shadow: 0 4px 35px rgba(139, 1, 1, 0.1); }

.sec-title {
  font-family: $mfont;
  font-size: 42px;
  font-weight: 400;
  color: #3d485f;
  span {
    color: #ddad70; } }
